import huxing from '@/pages/home/Img/components/huxing.vue'
import loupan from '@/pages/home/Img/components/loupan.vue'
import peitao from '@/pages/home/Img/components/peitao.vue'
import shapan from '@/pages/home/Img/components/shapan.vue'
import zhengzhao from '@/pages/home/Img/components/zhengzhao.vue'
export default{
	components:{
		huxing,
		loupan,
		peitao,
		shapan,
		zhengzhao
	},
	data(){
		return{
			scrollLeft: '',
			tabIndex: 0,
			tabBars: [{
					name: '楼盘',
					id: 'loupan'
				},
				{
					name: '沙盘',
					id: 'shapan'
				},
				{
					name: '户型',
					id: 'huxing'
				},
				{
					name: '配套',
					id: 'peixun'
				},
				{
					name: '售楼',
					id: 'shoulou'
				},
				{
					name: '证照',
					id: 'zhengzhao'
				}
			],
		}
	},
	computed: {
		currentTabKey() {
			return this.tabBars[this.tabIndex].id;
		}
	},
	methods: {
			async changeTab(e) {
				let index = e.detail.current;
				if (this.isClickChange) {
					this.tabIndex = index;
					this.isClickChange = false;
					return;
				}
				let tabBar = await this.getElSize('tab-bar'),
					tabBarScrollLeft = tabBar.scrollLeft;
				let width = 0;
	
				for (let i = 0; i < index; i++) {
					let result = await this.getElSize(this.tabBars[i].id);
					width += result.width;
				}
				let winWidth = uni.getSystemInfoSync().windowWidth,
					nowElement = await this.getElSize(this.tabBars[index].id),
					nowWidth = nowElement.width;
				if (width + nowWidth - tabBarScrollLeft > winWidth) {
					this.scrollLeft = width + nowWidth - winWidth;
				}
				if (width < tabBarScrollLeft) {
					this.scrollLeft = width;
				}
				this.isClickChange = false;
				this.tabIndex = index; //一旦访问data就会出问题
			},
			getElSize(id) {
				//得到元素的size
				return new Promise((res, rej) => {
					uni.createSelectorQuery()
						.select('#' + id)
						.fields({
								size: true,
								scrollOffset: true
							},
							data => {
								res(data);
							}
						)
						.exec();
				});
			},
			async tapTab(index) {
				//点击tab-bar
				if (this.tabIndex === index) {
					return false;
				} else {
					let tabBar = await this.getElSize('tab-bar'),
						tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
					this.scrollLeft = tabBarScrollLeft;
					this.isClickChange = true;
					this.tabIndex = index;
				}
			}
		}
	
}
